.row {
    margin-left: 20px;
    font-weight: bold;
    font-size: 1.08rem;
    color: white;
  }
  .row__posters {
    display: flex;
    overflow: hidden;
    overflow-x: scroll;
    padding: 20px;
  }
  
  .row__posters::-webkit-scrollbar {
    display: none;  /*scroll disappear*/
  }

  .row__posters {
      -ms-overflow-style: none;
      scrollbar-width: 0;
  }  
  .row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
    border-radius: 1vw;
  }
  
  .row__poster:hover {
    transform: scale(1.08);
  }
  
  .row__posterLarge {
    max-height: 250px;
  }
  
  .row__posterLarge:hover {
    transform: scale(1.09);
  }
